import Vue from 'vue';
import Router from 'vue-router';
import login from '@/components/login/login';
import footerTab from '@/components/footerTab/footerTab';
import home from '@/components/home/home';
import my from '@/components/my/my';
import xcheng from '@/components/xcheng/xcheng';
import planTrip from '@/components/xcheng/planTrip/planTrip';
import historyTrip from '@/components/xcheng/historyTrip/lishixingcheng';
import tripNotes from '@/components/xcheng/tripNotes/xingchengsuiji';
import fabusuiji from '@/components/xcheng/tripNotes/fabusuiji.vue';
import kjfw from '@/components/xcheng/tripNotes/kejianfanwei.vue';
import xchengtixing from '@/components/xcheng/xchengtixing/xchengtixing.vue';
import xchengtixingDetail from '@/components/xcheng/xchengtixing/xchengtixingDetail.vue';
import xcInfo from '@/components/xcheng/xingchengchakan/xingchengck';
import msg_xcInfo from '@/components/xcheng/xingchengchakan/msg_xingchengck';
import dayDetailchakan from '@/components/xcheng/xingchengchakan/dayDetailchakan';
import xingchengxiugai from '@/components/xcheng/xingchengxiugai/xingchengxiugai';
import dayDetailxiugai from '@/components/xcheng/xingchengxiugai/dayDetailxiugai';
import xcAdd from '@/components/xcheng/xingchengtianjia/xingchengtianjia';
import xcDayAdd from '@/components/xcheng/xingchengtianjia/dayDetailAdd';
import citySelect from '@/components/xcheng/citySelect/citySelect.vue';
import help from '@/components/help/help';
import teamManage from '@/components/teamManage/teamManage';
import tmTeam from '@/components/teamManage/children/tmTeam';
import tmContact from '@/components/teamManage/children/tmContact';
import teamAdd from '@/components/teamManage/teamAdd/teamAdd';
import teamView from '@/components/teamManage/teamView/teamView';
import msg_teamView from '@/components/teamManage/teamView/msg_teamView';
import newsRelease from '@/components/teamManage/newsRelease/newsRelease';
import messages from '@/components/my/pages/messages';
import modHeadIcon from '@/components/my/pages/modHeadIcon';
import modPhone from '@/components/my/pages/modPhone';
import modPwd from '@/components/my/pages/modPwd';
import msgsList from '@/components/my/pages/msgsList';
import teamNotice from '@/components/my/pages/msgList/teamNotice';
import teamCircular from '@/components/my/pages/msgList/teamCircular';
import locationShare from '@/components/my/pages/msgList/locationShare';
import teamHelp from '@/components/my/pages/msgList/teamHelp';
import teamInvite from '@/components/my/pages/msgList/teamInvite';
import systemNotice from '@/components/my/pages/msgList/systemNotice';
import teamNoticeInfo from '@/components/my/pages/msgList/teamNoticeInfo';
import msg_teamNoticeInfo from '@/components/my/pages/msgList/msgTeamNoticeInfo';
import signInfo from '@/components/my/pages/msgList/signInfo';
import msg_signInfo from '@/components/my/pages/msgList/msg_SignInfo';
import teamHelpInfo from '@/components/my/pages/msgList/teamHelpInfo';
import msg_teamHelpInfo from '@/components/my/pages/msgList/msgTeamHelpInfo';
import travelList from '@/components/my/pages/msgList/travelList';
import apparatus from '@/components/home/apparatus/apparatus';
import indexteamHelp from '@/components/home/teamHelp/teamHelp';
import warningList from '@/components/home/warning/warningList';
import warningDet from '@/components/home/warning/warningDet';
import msg_warningDet from '@/components/home/warning/msg_warningDet';
import commonHelp from '@/components/help/commonHelp';
import teamSet1 from '@/components/teamManage/teamView/teamSet/teamSet';
import teamSet2 from '@/components/teamManage/teamView/teamSet/teamSet2';
import updateContact from '@/components/teamManage/updateContact/updateContact';
import serviceAgreement from '@/components/comPage/serviceAgreement';
import device from '@/components/my/pages/device';
import Settings from '@/components/my/pages/settings';
import myTrip from '@/components/my/pages/myTrip';
import Register from '@/components/login/register';
import personInfo from '@/components/my/pages/personInfo';
import Suggestion from '@/components/my/pages/suggestion';
import AboutMe from '@/components/my/pages/aboutMe';
import ForgetPwd from '@/components/login/forgetPwd';
Vue.use(Router);

export default new Router({
  isBack:false,
  routes: [
    {
      path: '/login',
      name: 'login',
      components: {
        body: login
      }
    },
    {
      path: '/register',
      name: 'register',
      components: {
        body: Register
      }
    },
    {
      path: '/home',
      name: 'home',
      components: {
        body: home,
        footer: footerTab
      },
      mate: {keepAlive: true}
    },
    {
      path: '/my',
      name: 'my',
      components: {
        body: my,
        footer: footerTab
      },
      mate: {keepAlive: true}
    },
    {
      path: '/modHeadIcon',
      name: 'modHeadIcon',
      components: {
        body: modHeadIcon
      }
    },
    {
      path: '/modPhone',
      name: 'modPhone',
      components: {
        body: modPhone
      }
    },
    {
      path: '/modPwd',
      name: 'modPwd',
      components: {
        body: modPwd
      }
    },
    {
      path: '/xcheng',
      name: 'xcheng',
      components: {
        body: xcheng,
        footer: footerTab
      },
      children: [
        {
          path: '',
          components:{
            xcMain:planTrip
          }
        },
        {
          path: 'historyTrip',
          components:{
            xcMain:historyTrip
          }
        },
        {
          path: 'tripNote',
          components:{
            xcMain:tripNotes
          }
        }
      ],
      mate: {keepAlive: true}
    },
    {
      path: '/xcInfo',
      name: 'xcInfo',
      components: {
        body: xcInfo
      },
      mate: {keepAlive: true}
    },
    {
      path: '/msg_xcInfo',
      name: 'msg_xcInfo',
      components: {
        body: msg_xcInfo
      },
      mate: {keepAlive: true}
    },
    {
      path: '/dayDetailchakan',
      name: 'dayDetailchakan',
      components: {
        body: dayDetailchakan
      },
      mate: {keepAlive: true}
    },
    {
      path: '/xingchengxiugai',
      name: 'xingchengxiugai',
      components: {
        body: xingchengxiugai
      },
      mate: {keepAlive: true}
    },
    {
      path: '/dayDetailxiugai',
      name: 'dayDetailxiugai',
      components: {
        body: dayDetailxiugai
      },
      mate: {keepAlive: true}
    },
    {
      path: '/xcAdd',
      name: 'xcAdd',
      components: {
        body: xcAdd
      },
      mate: {keepAlive: true}
    },
    {
      path: '/xcDayAdd',
      name: 'xcDayAdd',
      components: {
        body: xcDayAdd
      },
     mate: {keepAlive: true}
    },
    {
      path: '/citySelect',
      name: 'citySelect',
      components: {
        body: citySelect
      },
      mate: {keepAlive: true}
    },
    {
      path: '/xchengtixing',
      name: 'xchengtixing',
      components: {
        body: xchengtixing
      },
      mate: {keepAlive: true}
    },
    {
      path: '/xchengtixingDetail',
      name: 'xchengtixingDetail',
      components: {
        body: xchengtixingDetail,
      },
      mate: {keepAlive: true}
    },
    {
      path: '/fabusuiji/',
      name: 'fabusuiji',
      components: {
        body: fabusuiji
      },
      mate: {keepAlive: true}
    },
    {
      path: '/kjfw/',
      name: 'kjfw',
      components: {
        body: kjfw
      },
      mate: {keepAlive: true}
    },
    {
      path: '/help',
      name: 'help',
      components: {
        body: help
      },
      mate: {keepAlive: true}
    },
    {
      path: '/teamManage',
      name: 'teamManage',
      components: {
        body: teamManage,
        footer: footerTab
      },
      children: [
        {path: '', component: tmTeam},
        {path: 'tmContact', component: tmContact}
      ],
      mate: {keepAlive: true}
    },
    {
      path: '/teamAdd',
      name: 'teamAdd',
      components: {
        body: teamAdd
      },
      mate: {keepAlive: true}
    },
    {
      path: '/teamView',
      name: 'teamView',
      components: {
        body: teamView
      },
      mate: {keepAlive: true}
    },
    {
      path: '/msg_teamView',
      name: 'msg_teamView',
      components: {
        body: msg_teamView
      },
      mate: {keepAlive: true}
    },
    {
      path: '/newsRelease',
      name: 'newsRelease',
      components: {
        body: newsRelease
      }
    },
    {
      path: '/msgList',
      name: 'msgList',
      components: {
        body: messages
      },
      children: [
        {path: '', component: msgsList},
        {path: 'teamNotice', component: teamNotice},
        {path: 'teamCircular', component: teamCircular},
        {path: 'locationShare', component: locationShare},
        {path: 'teamHelp', component: teamHelp},
        {path: 'teamInvite', component: teamInvite},
        {path: 'systemNotice', component: systemNotice},
        {path: 'teamNoticeInfo', component: teamNoticeInfo},
        {path: 'msg_teamNoticeInfo', component: msg_teamNoticeInfo},
        {path: 'signInfo', component: signInfo},
        {path: 'msg_signInfo', component: msg_signInfo},
        {path: 'teamHelpInfo', component: teamHelpInfo},
        {path: 'msg_teamHelpInfo', component: msg_teamHelpInfo},
        {path: 'travelList/:backURL', component: travelList}
      ],
      mate: {keepAlive: true}
    },
    {
      path: '/apparatus',
      name: 'apparatus',
      components: {
        body: apparatus
      },
      mate: {keepAlive: true}
    },
    {
      path: '/indexteamHelp',
      name: 'indexteamHelp',
      components: {
        body: indexteamHelp
      },
      mate: {keepAlive: true}
    },
    {
      path: '/warningList',
      name: 'warningList',
      components: {
        body: warningList
      }
    },
    {
      path: '/warningDet',
      name: 'warningDet',
      components: {
        body: warningDet
      }
    },
    {
      path: '/msg_warningDet',
      name: 'msg_warningDet',
      components: {
        body: msg_warningDet
      }
    },
    {
      path: '/commonHelp',
      name: 'commonHelp',
      components: {
        body: commonHelp
      }
    },
    {
      path: '/teamSet1',
      name: 'teamSet1',
      components: {
        body: teamSet1
      }
    },
    {
      path: '/teamSet2',
      name: 'teamSet2',
      components: {
        body: teamSet2
      }
    },
    {
      path: '/updateContact',
      name: 'updateContact',
      components: {
        body: updateContact
      }
    },
    {
      path: '/serviceAgreement',
      name: 'serviceAgreement',
      components: {
        body: serviceAgreement
      }
    },
    {
      path: '/setting',
      name: 'setting',
      components: {
        body: Settings
      }
    },
    {
      path: '/myTrip',
      name: 'myTrip',
      components: {
        body: myTrip
      }
    },
    {
      path: '/device',
      name: 'device',
      components: {
        body: device
      }
    },
    {
      path: '/personInfo',
      name: 'personInfo',
      components: {
        body: personInfo
      }
    },
    {
      path: '/suggestion',
      name: 'suggestion',
      components: {
        body: Suggestion
      }
    },
    {
      path: '/aboutMe',
      name: 'aboutMe',
      components: {
        body: AboutMe
      }
    },
    {
      path: '/forgetPwd',
      name: 'forgetPwd',
      components: {
        body: ForgetPwd
      }
    }
  ]
});



