<style scoped>
.tmContactBox{text-align: left;background: #fff;height:100%;}
 .tmContactBox .avatar{
   width: 0.28rem;
   height: 0.28rem;
   top:0.16rem;
 }
</style>
<style>
  .contactList  .mint-loadmore{height:100%;}
</style>
<template>
  <transition name="slide">
    <div class="tmContactBox">
      <div @click="addContact">
        <mt-cell title="添加手机联系人" is-link></mt-cell>
      </div>
      <mt-loadmore :top-method="loadTop" ref="loadmore">
      <div class="contactList">
        <mt-cell-swipe v-for="(item ,index) in contact"
                       :title=item.name
                       :label=item.phone.toString()
                       :right="[
                      {
                        content: '编辑',
                        style: { background: '#aeaeae', color: '#fff'},
                        handler: () => updateContact(item.phone,item.name)
                      },
                      {
                        content: '删除',
                        style: { background: '#e5433a', color: '#fff' },
                        handler: () => delContact(index,item.id)
                      }
                    ]">
          <img slot="icon" v-bind:src="item.headIconUrl | NoImg" class="avatar">
        </mt-cell-swipe>
      </div>
      </mt-loadmore>
    </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamManage',
    data () {
      return {
        loginUser:{},
        contact:{}
      }
    },
    created:function(){
      tmSetTab(2);
      this.loginUser=LoginUser.getUser();
      this.contactList(0,1000);
    },
    activated:function(){
      tmSetTab(2);
      this.loginUser=LoginUser.getUser();
      this.contactList(0,1000);
    },
    methods:{
      loadTop:function() {
        this.contactList(0,1000);
        this.$refs.loadmore.onTopLoaded();
      },
      contactList:function (pageNum,pageSize) {
        let it = this;
        //通讯录列表
        this.$axios({
          method: 'post',
          url: '/contact/getContactlist',
          data:this.$qs.stringify({
            userId:it.loginUser.userId,
            pageNum:pageNum,
            pageSize:pageSize
          })
        }).then(function (response) {
          it.contact = response.data.data.list;
        });
      },
      updateContact:function (phone,contactName) {
        this.$router.push({path: '/updateContact', query: { phone:phone,contactName:contactName }});
      },
      addContact:function(){
        appMethod = this.updateList;
        window.location.href="myapp://parameter?type=AddContactToMember&method=addContact"
      },
      updateList:function(){
        this.contactList(0,1000);
      },
      delwiContact:function (idx,id) {
        //通讯录删除
        let it = this;
        MessageBox.confirm('确定执行此操作?').then(action => {
          this.$axios({
            method: 'post',
            url: 'contact/deleteContacts',
            data:this.$qs.stringify({
              userId:it.loginUser.userId,
              id:id
            })
          }).then(function (response) {

            if(response.data.code >= 0){
              it.contact = it.contact.slice(0,idx).concat(it.contact.slice(idx+1,it.contact.length));
            }
          });
        });
      }
    },
    components:[Loadmore]
  }
</script>




