  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .msgContent{
    color: #666;
    opacity: 0.8;
    position: relative;
  }
  .msgContent span{
    display: inline-block;
    padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
    position: absolute;
    top:-0.03rem;
    display: block;
    width: 0.16rem;
    height: 0.18rem;
    background-image: url("./xiaoxi.png");
    background-size: 100% 100%;
  }
  .msg_sign{
    padding-top: 0.04rem;
  }
  .msg_sign span label{
    color: #666;
    padding-left: 6px;
  }
  .msg_sign span i{
    display: inline-block;
    width: 0.16rem;
    height: 0.2rem;
    background-image: url("./dingwei.png");
    background-size: 100% 100%;
    position: relative;
    top:3px;
    margin-right: 0.05rem;
  }
  .teamHelp .hdBody{
    height: calc(100vh - 60px - 40px);
  }
</style>
<template>
  <transition name="slide">
  <div class="msgBody teamHelp">
    <mt-header title="团队求助">
      <router-link to="/home" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="notices hdBody">
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
            <div class="box" @click="helpInfo(idx)" v-for="(msg,idx) in msgList">
              <div><label>{{msg.createTime|FormartDate}}</label></div>
              <div class="msgTitle">
                求助人：{{msg.fuserName}}
              </div>
              <div class="msgContent">
                <i class="icon-xiaoxi"></i>
                <span>{{msg.fdesc}}</span>
              </div>
              <div class="msg_sign">
                <span><i></i><label>{{msg.faddress}}</label></span>
              </div>
              <span v-if="msg.readed!==true" class="red-point"></span>
            </div>
        </div>
      </mt-loadmore>
      <!--<div class="box" @click="helpInfo">-->
        <!--<div><label>2017年5月21日</label></div>-->
        <!--<div class="msgTitle">-->
          <!--求助人：XXXX-->
        <!--</div>-->
        <!--<div class="msgContent">-->
          <!--<i class="icon-xiaoxi"></i>-->
          <!--<span>大家在希尔顿酒店大门口集合，带好各自行李和随 身物品办理入住。</span>-->
        <!--</div>-->
        <!--<div class="msg_sign">-->
          <!--<span><i></i><label>王庄路1号院，海淀区，北京 中国</label></span>-->
        <!--</div>-->
      <!--</div>-->
      <!--<div class="box" @click="helpInfo">-->
        <!--<div><label>2017年5月21日</label></div>-->
        <!--<div class="msgTitle">-->
          <!--求助人：XXXX-->
        <!--</div>-->
        <!--<div class="msgContent">-->
          <!--<i class="icon-xiaoxi"></i>-->
          <!--<span>大家在希尔顿酒店大门口集合，带好各自行李和随 身物品办理入住。</span>-->
        <!--</div>-->
        <!--<div class="msg_sign">-->
          <!--<span><i></i><label>王庄路1号院，海淀区，北京 中国</label></span>-->
        <!--</div>-->
      <!--</div>-->
    </div>
    <div class="send-btn">
      <router-link to="../indexteamHelp">
        <mt-button type="primary"  size="large" >发送团队求助</mt-button>
      </router-link>
    </div>
  </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamHelp',
    data () {
      return {
        pageNum:1,
        pageSize:20,
        allLoaded:false,
        msgList: []
      }
    },
    components:[ Loadmore ],
    activated:function(){
      this.queryList();
    },
    created:function(){
      this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      helpInfo:function(idx){
        this.$router.push('/msgList/teamHelpInfo?id='+this.msgList[idx].id);
      },
      queryList:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/tgroupHelp/getPageList',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            pageNum:_this.pageNum,
            pageSize:_this.pageSize
          })
        }).then(function (response) {
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.msgList = response.data.data.list;
            }else{
              _this.msgList = _this.msgList.concat(response.data.data.list);
            }
          }
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        });
        //console.log(_this.msgList);
      }
    }
  }
</script>




