<template>
   <div class="xcheng">
     <header class="mint-header">
       <ul class="headTab">
         <router-link to='/xcheng'>
           <li>计划行程</li>
         </router-link>
         <router-link to='/xcheng/historyTrip'>
           <li>历史行程</li>
         </router-link>
         <router-link to='/xcheng/tripNote'>
            <li class="no-right-border"><em v-if="newRecord!=0"></em><span>行程随记</span></li>
         </router-link>
       </ul>
     </header>
     <div class="tabContent">
       <router-view name="xcMain"></router-view>
     </div>
   </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default{
    name:'footerTab',
    data () {
      return {
        loginUser:{},
        newRecord:0,
        team:0
      }
    },
    activated:function(){
      this.getNewRecord();
    },
    created:function(){
      this.loginUser = LoginUser.getUser();
      this.getNewRecord();
      //this.getTeamNum();
    },
    methods:{
      getNewRecord:function(){
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/ttravel/countUserWeibo',
          data:this.$qs.stringify({userId:_this.loginUser.userId})
        }).then(function (response) {
          //console.log(response.data.data);
          _this.newRecord=response.data.data;
        });
      }
    }
  }
</script>

<style scoped>
a.router-link-exact-active li{
  background: #ffffff;
  color: #0e82e8;
  position: relative;
}
.headTab{
  width: 3rem;
  height: .25rem;
  line-height: .23rem;
  margin: 0 auto;
  display: block;
  border: .01rem solid #ffffff;
  border-radius: .05rem;
  border-color: rgba(255,255,255,0.5);
}
  .headTab li{
    width: 33.333333%;
    height: .25rem;
    float: left;
    line-height: .28rem;
    box-sizing: border-box;
    color: #ffffff;
    border-right:.01rem solid #ffffff ;
    border-color: rgba(255,255,255,0.5);
    position: relative;
  }
  .headTab li.no-right-border{
    border-right:none;
  }

.headTab em {
  position: absolute;
  top: -4px;
  right: -4px;
  width: 8px;
  height: 8px;
  border: 1px solid #fff;
  border-radius: 50%;
  background: linear-gradient(to bottom,#ff641f 0%,#ff1306 100%);
  display: block;
}

</style>




