        </div>
        <div class="warning">
          <div v-if="!(warningList && warningList.length>0)" class="no-data">暂无数据</div>
          <!-- 轮播新闻部分 -->
          <mt-swipe v-else :show-indicators="false" :stopPropagation="true" :auto="8000">
