          <div class="popNr">为了更好的给您服务，提供更精确的位置服务和免费的WiFi上网方式，请点击首页左上方的“扫码”，完成与专用定位通信设备的绑定，如需解绑或重新连接通信设备，请进入“我的-绑定设备管理”中操作</div>
          <p><input type="button" value="立即绑定" class="popIcon1" @click="scnaQRcode"></p>
