<!-- 获取本机ip地址 -->
<script>
  Date.prototype.Format = function (fmt) { //author: meizz
    var o = {
      "M+": this.getMonth() + 1, //月份
      "d+": this.getDate(), //日
      "h+": this.getHours(), //小时
      "m+": this.getMinutes(), //分
      "s+": this.getSeconds(), //秒
      "q+": Math.floor((this.getMonth() + 3) / 3), //季度
      "S": this.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  };
  import { Toast } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  import { Swipe, SwipeItem } from 'mint-ui';
  export default {
    name: 'home',
    data () {
      return {
        scnaQR:false,
        qrCodeInfo:{},
        time:'',
        cityInfo:{
           cityName:'',
           cityPing:''
        },
        weatherInfo:{},
        dataTime:'',
        loginUser:{},
        banner:[],
        warningList:[],
        mesgNum:0,
        teamMesgNum:{tdqz:0,tdtz:0,tdqd:0},
        locationSharingNum:0,
        isScroll:false
      }
    },
    components:[Swipe,SwipeItem],
    created:function(){
      this.loginUser = LoginUser.getUser();
      this.scnaQRpanduan();
      //this.refreshWeather();
    },
    activated:function(){
      document.getElementById("homeIndex").onscroll= this.scrolTop;
      this.isScroll = false;
      this.dealWithTime();
      this.loginUser = LoginUser.getUser();
      this.initData();
      this.time=new Date().Format("hh:mm");
      this.initDataNumber();
      this.refreshWeather();
    },
    methods:{
      getWordAddress:function(data){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'http://api.geonames.org/timezoneJSON?lat='+data.data.Latitude+'&lng='+data.data.Longitude+'&username=tb_map'
        }).then(function (res) {
           _this.dataTime = res.data.time;
        });
      },
      dealWithTime:function(){
        wordTimeMethod = this.getWordAddress;
        window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getWordAddress';
        if(timeInterval){
          clearInterval(timeInterval);
          timeInterval = null;
        }
        let _this = this;
        timeInterval = setInterval(function(){
          if(window.location.href.indexOf('home')!=-1){
            _this.initDataNumber();
            window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getWordAddress';
          }else{
            clearInterval(timeInterval);
            timeInterval = null;
          }
        },5*1000);
      },
      scrolTop:function () {
          let topDis = document.getElementById("homeIndex").scrollTop;
          document.getElementById("homeHead").style.opacity = topDis/70>=1?1:topDis/70;
          //setTimeout('document.getElementById("homeHead").style.backgroundColor="black"',1000)

          if(topDis == 0){
            this.isScroll = false;
          }else if(topDis != 0){
            this.isScroll = true;
          }
      },
      otherFunMap:function () {
        window.location.href='myapp://parameter?type=MapService';
      },
      otherFunDec:function () {
        window.location.href='myapp://parameter?type=Translate';
      },
      refreshWeather:function () {
        appMethod = this.weather;
        window.location.href='myapp://parameter?type=GetTravelIndex&method=getWeather';
        this.getWordAddress()
      },
      weather:function(data){
         this.cityInfo= {
           cityName:data.result.HeWeather5[0].basic.city,
           cityPing:data.result.HeWeather5[0].basic.cnty
         };
         this.weatherInfo = {
           dayTem:data.result.HeWeather5[0].daily_forecast[0].tmp.min+'℃/'+data.result.HeWeather5[0].daily_forecast[0].tmp.max+'℃',
           nowTime:data.result.HeWeather5[0].basic.update.loc
         };
      },
      initDataNumber:function(){
        this.getTdNum('2'); //团队求助
        this.getTdNum('0'); //团队通知
        this.getTdNum('7'); //团队签到
        this.locationSharing();
        this.getXcNum();
      },
      initData:function(){
        let it = this;
        this.$axios({
          method: 'post',
          url: 'banner/queryList',
          data:this.$qs.stringify({userId:it.loginUser.userId})
        }).then(function (response) {
          it.banner = response.data.data;
        });
        //预警信息新闻
        this.$axios({
          method: 'post',
          url: 'warningInfo/selectWarningInfo',
          data:this.$qs.stringify({userId:it.loginUser.userId})
        }).then(function (response) {
          it.warningList = response.data.data.list;
        });
      },
      getTdNum:function(ftype){
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/tmessage/countMessages',
          data:this.$qs.stringify({userId:_this.loginUser.userId,ftype:ftype})
        }).then(function (response) {
          if(ftype=='2'){
            _this.teamMesgNum.tdqz = response.data.data;
          }else if(ftype=='0'){
            _this.teamMesgNum.tdtz = response.data.data;
          }else if(ftype=='7'){
            _this.teamMesgNum.tdqd = response.data.data;
          }
        });
      },
      locationSharing:function(){
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/tlocation/countUserOpenGroup',
          data:this.$qs.stringify({userId:_this.loginUser.userId})
        }).then(function (response) {
          _this.locationSharingNum = response.data.num;
        });
      },
      getXcNum:function(){
        let _this=this;
        this.$axios({
          method: 'post',
          url: 'ttravel/selectCount',
          data:this.$qs.stringify({userId:_this.loginUser.userId})
        }).then(function (response) {
          _this.mesgNum=response.data.data;
        });
      },
      scnaQRpanduan:function () {
        if(this.loginUser.isBang === 1){
          this.scnaQR = false;
        }else if(this.loginUser.isBang === 0){
          this.scnaQR = true;
        }
      },
      closeModal:function(){
        this.scnaQR = false;
      },
      clickNews:function(idx){//点击轮播预警信息
        this.$router.push('/warningDet?warningInfoId='+this.warningList[idx].warningInfoId);
      },
      enterPostion:function(){//进入位置服务
         numMethod = this.initDataNumber;
         window.location.href = 'myapp://parameter?type=SharedLocation&method=appCallBack';
      },
      scnaQRcode:function () {
        this.scnaQR = false;
        scnaQRcodeMethod = this.getQRcodeData;
         /* 发送伪链接，调用手机扫描二维码 */
         window.location.href = 'myapp:/' +
           '/parameter?type=QrCodeScanning&method=callbackQRcode';
      },
      getQRcodeData:function(data){
          localStorage.setItem('twocData',JSON.stringify(data));
          this.$router.push('/apparatus?isHome=true&Mac='+data.data.Mac
            +'&WifiName='+data.data.WifiName+'&WifiPwd='+data.data.WifiPwd);
      },
      toMsgs:function(){
         this.$router.push('/msgList?param=home');
      },
      goExponent:function () {
        let instance = Toast("此功能正在构建中");
        setTimeout(() => {
          instance.close();
        }, 1000);
      }
    }
  }
</script>




